@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cantora+One&display=swap);
body {
    background-color: rgba(53, 58, 71, 0.952);
    
    font-family: 'Ubuntu', sans-serif;
    overflow-x: hidden;

}

#page-heading {
    color: #ffff00;
    margin: 7vw;
    font-size: 50px;
    text-transform: uppercase;
    font-family: 'Neucha', cursive;
    letter-spacing: 3px;
}

.bg-dark {
    color: rgb(221, 221, 221) !important;
}


.bg-dark::-webkit-input-placeholder {
    color: rgb(206, 206, 206);
    opacity: 1; /* Firefox */
}


.bg-dark:-ms-input-placeholder {
    color: rgb(206, 206, 206);
    opacity: 1; /* Firefox */
}


.bg-dark::placeholder {
    color: rgb(206, 206, 206);
    opacity: 1; /* Firefox */
}

.markdown, .mono {
    font-family: Consolas,monaco,monospace; 
}

@media screen and (max-width: 800px) {
    #page-heading {
        font-size: 50px;
    }
}

body {
    width: 98vw;
    height: 98vh;
}

@media screen and (min-width: 1000px) {
    #page-heading {
        font-size: 70px;
    }
}

h1#not-found-header {
    font-family: 'Cantora One', sans-serif;
    font-size: 12vw;
    margin-top: 20vh;
    text-align: center;
    color: #ffff00;
}
h3#not-found-subheader {
    font-family: 'Cantora One', sans-serif;
    font-size: 4vw;
    margin-top: 10vh;
    text-align: center;
    color: rgb(241, 241, 241);
}


.popup-container {
    z-index: 99;
    overflow-y: hidden;
}

.ta-form {
    margin-top: 4px;
}


.popup-container {
    width: 55vw;
    height: 80vh;
    background-color: #109c79ec;
    border-radius: 20px;
    border: solid gray 1px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 7px;
    padding-bottom: 14vh;
    overflow-x: hidden;
    overflow-y: hidden;
}

@media screen and (max-width: 360px) {
    .popup-container {
        width: 80vw;
    }
}   

#draggable-form {
    z-index: 1000;
}

#close-icon-wrapper {
    float: right;
    background-color: transparent;
    margin: 5px;
    border: solid 0px gray;
    border-radius: 50%;
    transition: 0.3s;
}

#close-icon-wrapper:hover {
    transition: 0.3s;
    background-color: rgba(212, 212, 212, 0.863);
}

.tip-start {
    font-size: 23px;
}

#popup-dontshow {
    margin-left: 4%;
    vertical-align: baseline;
    position: absolute;
    bottom: 0;

}

.tip-container {
    margin-left: 4%;
    color: rgb(255, 255, 255);
}


#tasks-container {
    overflow: hidden;
    position: relative;
    margin: auto;
}


.flex-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

.card {
    display: inline-block;
    position: relative;
    background: transparent;
    width: 230px;
    height: 350px;
    margin: 30px;
    border: none;
}

.face {
    width: 230px;
    height: 175px;
    border: none;
    transition: 0.4s;
}

.face1 {
    border: none;
    position: relative;
    display: flex;
    background-color: rgba(153, 153, 153);
    justify-content: center;
    align-items: center;
    padding: 5px;
    transform: translateY(100px);
    z-index: 20;
}

.face1 .card-content {
    opacity: 0.2;
    transition: 0.4s;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
}

.card:hover .face.face1 .card-content {
    opacity: 1;
}

.face.face1 .card-content h3 {
    text-align: center;
}

.card:hover .face.face1 {
    background-color: #16ecace7;
    transform: translateY(0);
}

.card:hover .face.face2 {
    transform: translateY(0);
}


.card-content img {
    max-width: 100px;
}

h3.task-title {
    margin: 10px 0 0;
}

.face2 {
    border: none;
    position: relative;
    display: flex;
    background-color: transparent;
    padding: 5px;
    box-sizing: border-box;
    box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.8);
    z-index: 1;
    background-color: rgb(230, 230, 230);
    transform: translateY(-75px);
}

.add-task-container.container {
    z-index: 1000;
}

.task-card-icon {
    width: 100px;
    height: 100px;
}

.face2 .card-content p {
    margin: 4px;
    padding: 0;
    width: 210px;
}


.face2 .card-content button {
    margin: 4px;
    display: inline-block;
    text-decoration: none;
    position: fixed;
    bottom: 5%;
    left: 5%;
}

.face2 .card-content .task-card-control {
    overflow: hidden;
    position: fixed;
    bottom: 10%;
    left: 70%;
}

.popup-container {
    overflow-y: scroll;
}

.control-icons {
    margin: 3px;
    cursor: pointer;
    display: inline-block;
}


/* Overriding some style on smaller screens */
@media screen and (max-width: 290px) {
    #tasks-container {
        margin: 0px;
    }
    .card {
        margin: 10px;
        width: 180px;
    }
    .face {
        width: 180px;
    }
}


